"use strict";

var classNames = require("classnames");
var React = require("react/addons");

var AppHealthComponent = require("../components/AppHealthComponent");

var AppComponent = React.createClass({
  displayName: "AppComponent",

  propTypes: {
    model: React.PropTypes.object.isRequired,
    router: React.PropTypes.object.isRequired
  },

  onClick: function onClick() {
    this.props.router.navigate("apps/" + encodeURIComponent(this.props.model.get("id")), { trigger: true });
  },

  render: function render() {
    var model = this.props.model;

    var runningTasksClassSet = classNames({
      "text-warning": !model.allInstancesBooted()
    });

    var statusClassSet = classNames({
      "text-warning": model.isDeploying()
    });

    return (
      // Set `title` on cells that potentially overflow so hovering on the
      // cells will reveal their full contents.
      React.createElement(
        "tr",
        { onClick: this.onClick },
        React.createElement(
          "td",
          { className: "overflow-ellipsis", title: model.get("id") },
          model.get("id")
        ),
        React.createElement(
          "td",
          { className: "text-right" },
          model.get("mem")
        ),
        React.createElement(
          "td",
          { className: "text-right" },
          model.get("cpus")
        ),
        React.createElement(
          "td",
          { className: "text-right" },
          React.createElement(
            "span",
            { className: runningTasksClassSet },
            model.formatTasksRunning()
          ),
          " / ",
          model.get("instances")
        ),
        React.createElement(
          "td",
          { className: "text-right health-bar-column" },
          React.createElement(AppHealthComponent, { model: model })
        ),
        React.createElement(
          "td",
          { className: "text-right" },
          React.createElement(
            "span",
            { className: statusClassSet },
            model.getStatus()
          )
        )
      )
    );
  }
});

module.exports = AppComponent;


