"use strict";

var classNames = require("classnames");
var React = require("react/addons");
var Moment = require("moment");

var Task = require("../models/Task");

function buildHref(host, port) {
  return "http://" + host + ":" + port;
}

function buildTaskAnchors(task) {
  var taskAnchors;
  var ports = task.get("ports");
  var portsLength = ports.length;

  if (portsLength > 1) {
    // Linkify each port with the hostname. The port is the text of the
    // anchor, but the href contains the hostname and port, a full link.
    taskAnchors = React.createElement(
      "span",
      { className: "text-muted" },
      task.get("host"),
      ":[",
      ports.map(function (p, index) {
        return React.createElement(
          "span",
          { key: p },
          React.createElement(
            "a",
            { className: "text-muted", href: buildHref(task.get("host"), p) },
            p
          ),
          index < portsLength - 1 ? ", " : ""
        );
      }),
      "]"
    );
  } else if (portsLength === 1) {
    // Linkify the hostname + port since there is only one port.
    taskAnchors = React.createElement(
      "a",
      { className: "text-muted", href: buildHref(task.get("host"), ports[0]) },
      task.get("host"),
      ":",
      ports[0]
    );
  } else {
    // Ain't no ports; don't linkify.
    taskAnchors = React.createElement(
      "span",
      { className: "text-muted" },
      task.get("host")
    );
  }

  return taskAnchors;
}

var TaskListItemComponent = React.createClass({
  displayName: "TaskListItemComponent",

  propTypes: {
    appId: React.PropTypes.string.isRequired,
    hasHealth: React.PropTypes.bool,
    isActive: React.PropTypes.bool.isRequired,
    onToggle: React.PropTypes.func.isRequired,
    task: React.PropTypes.object.isRequired,
    taskHealthMessage: React.PropTypes.string
  },

  handleClick: function handleClick(event) {
    // If the click happens on the checkbox, let the checkbox's onchange event
    // handler handle it and skip handling the event here.
    if (event.target.nodeName !== "INPUT") {
      this.props.onToggle(this.props.task);
    }
  },

  handleCheckboxClick: function handleCheckboxClick(event) {
    this.props.onToggle(this.props.task, event.target.checked);
  },

  render: function render() {
    var task = this.props.task;
    var hasHealth = !!this.props.hasHealth;
    var version = task.get("version").toISOString();
    var taskId = task.get("id");
    var taskUri = "#apps/" + encodeURIComponent(this.props.appId) + "/" + encodeURIComponent(taskId);

    var taskHealth = task.getHealth();

    var listItemClassSet = classNames({
      "active": this.props.isActive
    });

    var healthClassSet = classNames({
      "health-dot": true,
      "health-dot-error": taskHealth === Task.HEALTH.UNHEALTHY,
      "health-dot-success": taskHealth === Task.HEALTH.HEALTHY,
      "health-dot-unknown": taskHealth === Task.HEALTH.UNKNOWN
    });

    var statusClassSet = classNames({
      "text-warning": task.isStaged()
    });

    var hasHealthClassSet = classNames({
      "text-center": true,
      "hidden": !hasHealth
    });

    var updatedAtNodeClassSet = classNames({
      "hidden": task.get("updatedAt") == null
    });

    var updatedAtISO;
    var updatedAtLocal;
    if (task.get("updatedAt") != null) {
      updatedAtISO = task.get("updatedAt").toISOString();
      updatedAtLocal = task.get("updatedAt").toLocaleString();
    }

    return React.createElement(
      "tr",
      { className: listItemClassSet },
      React.createElement(
        "td",
        { width: "1", className: "clickable", onClick: this.handleClick },
        React.createElement("input", { type: "checkbox",
          checked: this.props.isActive,
          onChange: this.handleCheckboxClick })
      ),
      React.createElement(
        "td",
        null,
        React.createElement(
          "a",
          { href: taskUri },
          taskId
        ),
        React.createElement("br", null),
        buildTaskAnchors(task)
      ),
      React.createElement(
        "td",
        { className: "text-center" },
        React.createElement(
          "span",
          { className: statusClassSet },
          task.get("status")
        )
      ),
      React.createElement(
        "td",
        { className: "text-right" },
        React.createElement(
          "span",
          {
            title: version },
          new Moment(version).fromNow()
        )
      ),
      React.createElement(
        "td",
        { className: "text-right" },
        React.createElement(
          "time",
          { className: updatedAtNodeClassSet,
            dateTime: updatedAtISO,
            title: updatedAtISO },
          updatedAtLocal
        )
      ),
      React.createElement(
        "td",
        { className: hasHealthClassSet, title: this.props.taskHealthMessage },
        React.createElement("span", { className: healthClassSet })
      )
    );
  }
});

module.exports = TaskListItemComponent;


